body {
  overflow-x: hidden;
  padding-top: 70px;
  margin: 0 auto;
}

.no-gutters{
  padding-left: 0;
  padding-right: 0;
}

#wrapper {
  padding-left: 0;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

#wrapper.toggled {
  padding-left: 250px;
}

#sidebar-wrapper {
  z-index: 1000;
  position: fixed;
  left: 250px;
  width: 0;
  height: 100%;
  margin-left: -250px;
  overflow-y: auto;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-box-shadow: 1px 1px 1px grey;
          box-shadow: 1px 1px 1px grey;
}

#wrapper.toggled #sidebar-wrapper {
  width: 250px;
}

#page-content-wrapper {
  width: 100%;
  position: absolute;
  padding: 15px;
}

#wrapper.toggled #page-content-wrapper {
  position: absolute;
  margin-right: -250px;
}

.navbar {
  margin-bottom: 0;
}


/* Sidebar Styles */

.sidebar-nav {
  position: absolute;
  top: 0;
  width: 250px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.navbar-panel {
  position: relative;
  top: -20px;
}

.navbar {
  border: 0px;
}

.logo {}

.top-nav {
  border-radius: 0px;
}

.middle-nav {
  padding-top: 40px;
  padding-bottom: 75px;
  background-color: white;
  background-image: url("../images/gears.png");
  background-repeat: no-repeat;
  background-position: right;
}

.bottom-nav {
  -webkit-box-shadow: 0px 2px 4px grey;
          box-shadow: 0px 2px 4px grey;
  border-radius: 0px;
}

.main-nav-scrolled {
  position: fixed;
  width: 100%;
  margin-top: -240px;
  z-index: 1;
  width: 102%;
}

.navbar-form {
  border: 0;
}

.form-horizontal .form-group {
  margin: 8px;
}

input {
  border-radius: 0;
}

.middle-nav-input-group {
  -webkit-box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.63);
          box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.63)
}

h1 {
  text-align: center;
  padding-bottom: 50px;
}
.top-search{
  -webkit-animation: fadein 300ms;
          animation: fadein 300ms;
}

@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.hidden{
-webkit-transition: visibility 0s 2s, opacity 2s linear;
-o-transition: visibility 0s 2s, opacity 2s linear;
transition: visibility 0s 2s, opacity 2s linear;
}

@-webkit-keyframes fadeout {
    from { opacity: 0; }
    to   { opacity: 1; }
}

@keyframes fadeout {
    from { opacity: 0; }
    to   { opacity: 1; }
}



